import './App.css';
import React from 'react';
import {useState, useEffect} from 'react';
import { Button, FormControl, Input, InputLabel, List} from '@material-ui/core'
import Todo from './Todo';
import db from './firebase';
import firebase from 'firebase';

function App() {

  const [tasks, setTasks] = useState([]);
  const [input, setInput] = useState('');

  useEffect(() => {
    db.collection('tasks').orderBy('time', 'desc').onSnapshot(snapshot => {
      setTasks(snapshot.docs.map(doc => doc.data().task));
    })
  }, [])

  const addTask = (e) => {
    e.preventDefault();

    db.collection('tasks').add({
      task: input,
      time: firebase.firestore.FieldValue.serverTimestamp()
    });

    setInput('');
  }
  const setInputFunc = (e) => {
    setInput(e.target.value);
  }

  return (
    <div className="App">
      <h1>Hello, it's just me building a todo list app </h1>
      <form>
        <FormControl>
          <InputLabel>write your task here</InputLabel>
          <Input type='text' value={input} onChange={setInputFunc}></Input>
        </FormControl>
          <Button color='primary' variant='contained' disabled={!input} type='submit' onClick={addTask}>Add</Button>
      </form>
      <List>
      {
        tasks.map((task, index) => {
          return <Todo key={index} task={task}/>
        })
      }
      </List>
    </div>
  );
}

export default App;
